import _ from 'lodash'
import PopJs from './pop-js'

class TbModel extends PopJs {
  constructor(id) {
    super(id)
    this.id = id

    this.modelType = 'TB'

    this.compartment = {
      susceptible: 0,
      latentEarly: 0,
      latentRemote: 0,
      infectious: 0,
      diagnosed: 0,
      treated: 0,
      dead: 0,
      susceptibleAgain: 0
    }

    this.defaultParams = {
      initPopulation: 5000,
      initPrevalence: 300,
      beta: 5,
      betaAgain: 5,
      latentChangeRate: 1 / 100,
      infectionEarlyRate: 1.1 / 1000,
      infectionLateRate: 5.5 / 1000000,
      diagnosisRate: 1 / 365 / 3,
      treatmentRate: 1 / 14,
      recoverRate: 2 / 365,
      deathRate1: 1 / 365 / 3,
      deathRate2: 1 / 365 / 10
    }

    this.param = _.cloneDeep(this.defaultParams)

    this.varEvents.push(['susceptible', 'latentEarly', 'rateForce'])
    this.varEvents.push(['susceptibleAgain', 'latentEarly', 'rateForceAgain'])
    this.paramEvents.push(['latentEarly', 'latentRemote', 'latentChangeRate'])
    this.paramEvents.push(['latentEarly', 'infectious', 'infectionEarlyRate'])
    this.paramEvents.push(['latentRemote', 'infectious', 'infectionLateRate'])
    this.paramEvents.push(['infectious', 'diagnosed', 'diagnosisRate'])
    this.paramEvents.push(['diagnosed', 'treated', 'treatmentRate'])
    this.paramEvents.push(['infectious', 'dead', 'deathRate1'])
    this.paramEvents.push(['treated', 'dead', 'deathRate2'])
    this.paramEvents.push(['treated', 'susceptibleAgain', 'recoverRate'])

    this.guiParams = [
      {
        key: 'initPopulation',
        value: 5000,
        max: 100000,
        interval: 1,
        placeHolder: '',
        label: 'Population',
        decimal: 0
      },
      {
        key: 'initPrevalence',
        value: 300,
        max: 10000,
        interval: 1,
        placeHolder: '',
        label: 'Initial Prevalence',
        decimal: 0
      },
      {
        key: 'years',
        value: 2,
        max: 20,
        interval: 0.1,
        placeHolder: '',
        label: 'Years',
        decimal: 1
      },
      {
        key: 'beta',
        value: 0.5,
        interval: 0.01,
        placeHolder: '',
        max: 10,
        label: 'Contact Rate',
        decimal: 2
      },
      {
        key: 'betaAgain',
        value: 0.5,
        interval: 0.01,
        placeHolder: '',
        max: 10,
        label: 'Contact Rate Again',
        decimal: 2
      },
      {
        key: 'caseDetectionRate',
        value: 0.1,
        interval: 0.01,
        placeHolder: '',
        max: 1,
        label: 'Case Detection Rate',
        decimal: 1
      }
    ]

    this.interventionParams = [
      {
        key: 'interventionYear',
        value: 0.2,
        max: 20,
        interval: 0.1,
        placeHolder: '',
        label: 'Start Year',
        decimal: 2
      },
      {
        key: 'infectionEarlyRate',
        value: 0.2 * 1.1 / 1000,
        interval: 0.00001,
        max: 0.001,
        placeHolder: '',
        label: 'Infection Rate',
        decimal: 5
      },
      {
        key: 'infectionLateRate',
        value: 5.5 / 1000000,
        interval: 0.00001,
        max: 0.0001,
        placeHolder: '',
        label: 'Infection Remote Rate',
        decimal: 7
      },
      {
        key: 'treatmentRate',
        value: 1 / 14,
        interval: 0.01,
        max: 0.1,
        placeHolder: '',
        label: 'Treatment Rate',
        decimal: 3
      },
      {
        key: 'deathRate1',
        value: 1 / 365 / 3,
        interval: 0.001,
        max: 0.01,
        placeHolder: '',
        label: 'Infection Death Rate',
        decimal: 5
      }
    ]
  }

  calcExtraParams() {
    this.param.diagnosisRate =
      (this.param.caseDetectionRate / (1 - this.param.caseDetectionRate)) *
      (1 / 365 / 3)
  }

  calcVars() {
    this.var.population = _.sum(_.values(this.compartment))
    this.var.rateForce =
      (this.param.beta * this.compartment.infectious) / this.var.population
    this.var.rateForceAgain =
      (this.param.betaAgain * this.compartment.infectious) / this.var.population
  }

  calcDiagnosticVars() {
    this.var.prevalence =
      (this.compartment.infectious / this.var.population) * 100000
    this.var.incidence = 0
    this.var.mortality = 0
    for (let [, to, val] of this.events) {
      if (to === 'infectious') {
        this.var.incidence += val
      }
      if (to === 'dead') {
        this.var.mortality += val
      }
    }
  }
}

let epiModels = [
  {
    Class: TbModel,
    name: 'Tuberculosis'
  }
]

export { epiModels }



